import React from "react";

const CopyTextButton = ({ text }) => {
  const copyToClipboard = () => {
    navigator.clipboard
      .writeText(text)
      .then(() => {
        alert("!");
      })
      .catch((err) => {
        console.error("    :", err);
        alert("  .");
      });
  };

  return (
    <button
      style={{
        marginLeft: "0.5rem",
        backgroundColor: "transparent",
        border: "1px solid #929090",
        cursor: "pointer",
        color: "black",
      }}
      onClick={copyToClipboard}
    >
      
    </button>
  );
};

export default CopyTextButton;
